@charset "utf-8";

/* _frame_pc_sitemap.scss */

.sitemap_panel{
  @include user-select(none);
}

#sitemapHost {
  font-size: 12px;
  color:#333;
  background-color:#FFF;
  height: 100%;
}

#sitemapSelectContainer {
  display:none;
  position:absolute;
  left:0;
  right:124px;
  top:0;
  height:48px;
}

#sitemapSelectContainer select {
  width:100%;
  height:100%;
  padding:10px 20px;
  border:0;
  background:rgba(0,0,0,0);
}

.sitemapTree {
  overflow:visible;
  clear:both;
  padding:10px 0;
}
.sitemapTree ul {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
}

.sitemapMinus, .sitemapPlus {
  display: inline-block;
  height: 11px;
  width: 11px;
  margin-top: 8px;
  margin-right: 2px;
  vertical-align:top;
  cursor: pointer;
}

.sitemapMinus {
  @include use-sprite-image(-60px);
}
.sitemapPlus {
  @include use-sprite-image(-90px);
  margin-top:5px;
}

.sitemapPageLink {
  margin-left: 0px;
  cursor: pointer;
}

.ico_sitemap_page {
  @include use-sprite-image(-30px);
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-bottom: -3px;
}
.ico_sitemap_folder {
  @include use-sprite-image(0);
}

.sitemapPageName {
  margin-left: 6px;
}

.sitemapNode {
  white-space:nowrap;
}

.sitemapPageLinkContainer {
  margin-left: 0;
  padding-bottom: 1px;
  height:26px;
  line-height:26px;
  vertical-align:top;
}

.sitemapExpandableNode {
  margin-left: 0px;
}

.sitemapHighlight {
  background-color : #e5f4f9;
  a{
    color:#2f6acd;
  }
}

.sitemapGreyedName {
  color: #AAA;
}

#sitemapToolbar {
  margin: 5px 5px 5px 5px;
  height: 22px;
}

#sitemapToolbar .sitemapToolbarButton {
  float: left;
  width: 22px;
  height: 22px;
  border: 1px solid transparent;
}


#sitemapToolbar .sitemapToolbarButton:hover {
  border: 1px solid rgb(0,157,217);
  background-color : rgb(166,221,242);
}

#sitemapToolbar .sitemapToolbarButton:active {
  border: 1px solid rgb(0,157,217);
  background-color : rgb(204,235,248);
}

#sitemapToolbar .sitemapToolbarButtonSelected {
  border: 1px solid rgb(0,157,217) !important;
  background-color : rgb(204,235,248) !important;
}

.sitemapLinkContainer {
  margin-top: 8px;
  padding-right: 5px;
  font-size: 12px;
}

.sitemapLinkField {
  width: 100%;
  font-size: 12px;
  margin-top: 3px;
}

.sitemapOptionContainer {
  margin-top: 8px;
  padding-right: 5px;
  font-size: 12px;
}

#sitemapOptionsDiv {
  margin-top: 5px;
  margin-left: 16px;
}

#viewSelectDiv {
  margin-left: 5px;
}

#viewSelect {
  width: 70%;
}

.sitemapUrlOption {
  padding-bottom: 5px;
}

.optionLabel {
  font-size: 12px;
}

.sitemapPopupContainer {
  display: none;
  position: absolute;
  background-color: #F4F4F4;
  border: 1px solid #B9B9B9;
  padding: 5px 5px 5px 5px;
  margin: 5px 0px 0px 5px;
  z-index: 1;
}

#adaptiveViewsContainer {
  margin-left: 0px;
  padding: 0px;
}

.adaptiveViewOption {
  padding: 2px;
}

.adaptiveViewOption:hover {
  background-color: rgb(204,235,248);
  cursor: pointer;
}

.currentAdaptiveView {
  font-weight: bold;
}

.adaptiveCheckboxDiv {
  height: 15px;
  width: 15px;
  float: left;
}

.checkedAdaptive {
  background: url('images/adaptivecheck.png') no-repeat center center;
}

#variablesContainer {
  max-height: 350px;
  overflow: auto;
}

.variableName {
  font-weight: bold;
}

.variableDiv {
  margin-bottom: 10px;
}

#variablesClearLink {
  color: #069;
  left: 5px;
}

.searchBoxHint {
  color: #AAA;
  font-style: italic;
}

#sitemapLinksPageName {
  font-weight: bold;
}

#sitemapOptionsHeader {
  font-weight: bold;
}
