@charset "UTF-8";

/* _frame_pc_multi.scss */

.btn_deselect_all_items{
  @include border-radius('12px');

  display:inline-block;
  overflow:hidden;
  position:absolute;
  top:7px;
  right:39px;
  height:27px;
  padding:0 10px;
  background:#53b3dd;
  border:0;
  font-size:13px;
  text-shadow: none;
  line-height:27px;
  color:#9be4f6;
  vertical-align:top;
  cursor:pointer;
  z-index:20;
}
.btn_deselect_all_items.on{
  background:#2f9ccd;
  color:#cbecfa;
}

.viewerPage_multiViews_area iframe::-webkit-scrollbar {
  display: none!important;
}

/* Device List */
.devices_list_container{
  bottom:52px;
}

.devices_list{
  position:relative;
  font-size:11px;

  .item{
    position: relative;
    border-bottom:1px solid #efefef;

    &:last-child{
      border-bottom:0;
    }
  }
  .img_device{
    position:absolute;
    top:41px;
    left:10px;
  }
}

.devices_list .category_name{
  position:relative;
  height:30px;
  padding-left:27px;
  border-bottom:1px solid #d0e2e9;
  background:#f6f6f6;
  color:#666;
  font-size:11px;
  line-height:30px;
  vertical-align:top;
  cursor:pointer;

  > strong{
    font-weight:normal;
  }

  &:before{
    @include use-sprite-image(-60px);

    display:inline-block;
    overflow:hidden;
    position:absolute;
    top:11px;
    left:12px;
    width:13px;
    height:11px;
    content:'';
  }
  &.unfold:before{
    @include use-sprite-image(-90px);

    top:10px;
  }
}

.list_model_series{
  border-left:1px solid #efefef;

  .item_inr{
    position:relative;
    height:30px;
    padding-left:29px;
    background:#fff;
    line-height:29px;
    vertical-align:top;
    cursor:pointer;

    &:hover{
      background-color:#e5f4f9;
    }
    &.checked .model_name{
      color:#2f6acd;
    }
    &.checked:before{
      @include border-radius('50%');

      display:inline-block;
      position:absolute;
      top:12px;
      left:15px;
      width:4px;
      height:4px;
      background:#6796f9;
      content: "" ;
    }

    .model_name{
      font-weight:normal;
      color:#666;
    }
  }
}

/* Contents (global style) */
.device_wrap {
  display: inline-block;
  position: relative;
  margin: 100px 35px 12px;
  color: #e0e0e0;

  .home_button {
    @include border-radius(9px);

    display: inline-block;
    position: absolute;
    left: 50%;
    bottom: -40px;
    width: 73px;
    height: 17px;
    margin-left: -37px;
    background: #e3e5e8;
    content: '';
  }

  .btn {
    display: inline-block;
    overflow: hidden;
    position: absolute;
    top: -37px;
    height: 23px;
    border: 0;
    background: transparent;
    line-height: 99px;
    vertical-align: top;

    &.btn_rotate {
      @include use-sprites-image-device(0, 0);

      right: 28px;
      width: 27px;

      &:hover {
        @include use-sprites-image-device(-60px, 0);
      }
    }
    &.btn_close {
      @include use-sprites-image-device(-32px, 0);

      right: 0;
      width: 23px;

      &:hover {
        @include use-sprites-image-device(-92px, 0);
      }
    }
  }

  .watchband {
    height: 90px;

    div {
      float: left;
      width: 60px;
      height: 90px;
      background: #ccc;
    }
  }

  .device_panel {
    @include border-radius(32px);

    padding: 53px 13px 65px;
    background: #ccc;
    border: 2px solid #ccc;
    vertical-align: top;

    &.on,
    &:hover {
      @include box-shadow(0, 2px, 0, #aeb0b2);

      border-color: #fff;
    }
  }
}

/* Contents > watchhands */
.device_wrap .watchband {
  &.top {
    .left { background: url("../images/watch_vtl.png"); }
    .middle { background: url("../images/watch_vtm.png") repeat-x; }
    .right { background: url("../images/watch_vtr.png"); }
  }

  &.bottom {
    .left { background: url("../images/watch_vbl.png"); }
    .middle { background: url("../images/watch_vbm.png") repeat-x; }
    .right { background: url("../images/watch_vbr.png"); }
  }
}

/* Contents > main panel > home button & status bar */
.device_wrap {
  &[data-soft-key='true'] .home_button {
    display: none !important;
  }

  &[data-device-vendor='Apple'] {
    .home_button {
      @include border-radius(50%);

      bottom: -52px;
      width: 37px;
      height: 37px;
      margin-left: -18px;
    }

    .status_bar {
      background: #f7f7f7;
      border-bottom: 1px solid #ccc;

      .ico {
        &.lft {
          @include use-sprites-image-device(0, -90px);

          display: block;
          width: 54px;
          left: 8px;
          margin-top: -6px;
        }
        &.cnt {
          @include use-sprites-image-device(0, -120px);

          display: block;
          left: 50%;
          width: 56px;
          margin-top: -6px;
          margin-left: -28px;
        }
        &.rgt {
          @include use-sprites-image-device(0, -150px);

          display: block;
          width: 60px;
          right: 9px;
          margin-top: -6px;
        }
      }
    }
  }

  &[data-device-category='Desktop'] .home_button,
  &[data-device-category='Smart Watch'] .home_button {
    display: none;
  }
}

/* Contents > main panel > title */
.device_wrap .device_title {
  @include ellipsis();

  position: absolute;
  top: -32px;
  left: 0;
  max-width: 70%;
  font-size: 14px;
  color: #797979;
}

/* Contents > main panel > screen */
.device_wrap .device_screen {
  position: relative;
  background: #fff;

  .screen_cont {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  .status_bar {
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    padding: 0;
    background-color: #707070;
    font-size: 12px;
    color: #fff;
    white-space: nowrap;

    .ico {
      display: inline-block;
      position: absolute;
      top: 50%;
      height: 16px;
      margin-top: -8px;
      &.rgt {
        @include use-sprites-image-device(0, -60px);

        right: 7px;
        width: 76px;
      }
      &.lft,
      &.cnt {
        display: none;
      }
    }
  }

  .soft_key {
    position: absolute;
    background-color: #4c4c4c;
    outline: 1px solid #333;
    bottom: 0;
    left: 0;
    right: 0;

    .ico {
      display: inline-block;
      position: absolute;
      top: 50%;
      width: 30px;
      height: 20px;
      margin-top: -10px;

      &.lft {
        @include use-sprites-image-device(0, -30px);

        left: 20%;
      }
      &.cnt {
        @include use-sprites-image-device(-30px, -30px);

        left: 50%;
        margin-left: -15px;
      }
      &.rgt {
        @include use-sprites-image-device(-60px, -30px);

        right: 20%;
      }
    }
  }
}

/* style for specific device */
.device_wrap[data-device-category='Smart Watch'] {
  .device_panel {
    @include border-radius(0px);

    padding: 0px 32px;
    border: none;
    border-left: 8px solid #f1f3f5;
    border-right: 8px solid #f1f3f5;

    &.on,
    &:hover {
      @include box-shadow(0, 0, 0, #aeb0b2);
    }
  }
}

/* Contents >  orientation:landscape */
.device_wrap[data-device-orientation='landscape'] {
  /* global layout */
  .watchband, .device_panel {
    float: left;
  }

  /* wathbands */
  .watchband {
    div {
      width: 90px;
      height: 60px;
      float: none;
    }

    &.top {
      .left { background: url("../images/watch_hlt.png"); }
      .middle { background: url("../images/watch_hlm.png") repeat-y; }
      .right { background: url("../images/watch_hlb.png"); }

    }

    &.bottom {
      .left { background: url("../images/watch_hrt.png"); }
      .middle { background: url("../images/watch_hrm.png") repeat-y; }
      .right { background: url("../images/watch_hrb.png"); }
    }
  }

  /* main panel */
  .device_panel {
    padding: 13px 65px 13px 53px;
  }

  /* main panel > home button */
  .home_button {
    top: 50%;
    left: auto;
    right: -42px;
    width: 17px;
    height: 73px;
    margin-top: -36px;
    margin-left: 0;
  }

  /* main panel > screen & soft key */
  .device_screen .soft_key {
    top: 0;
    bottom: 0;
    height: 100%;

    .ico {
      left: 0;
      right: auto;
      margin-top: 0;
      margin-left: 6px;

      &.lft {
        top: 20%;
      }
      &.cnt {
        top: 50%;
        margin-top: -10px;
      }
      &.rgt {
        top: 80%;
      }
    }
  }

  /* style for specific device: vendor -> apple */
  &[data-device-vendor='Apple'] .home_button {
    right: -52px;
    width: 37px;
    height: 37px;
    margin-top: -18px;
  }

  /* style for specific device: category -> smart watch */
  &[data-device-category='Smart Watch'] .device_panel {
    padding: 32px 0px;
    border: none;
    border-top: 8px solid #f1f3f5;
    border-bottom: 8px solid #f1f3f5;
  }
}