@charset "UTF-8";

/* _frame_pc_fullmode_menu.scss */

.page_nav_area{
  @include user-select(none);

  display:none;
  position: absolute;
  top:0;
  right:0;
  z-index:90;
  margin:17px 17px 0 17px;
  padding-bottom:20px;
  opacity:.3;
}
.page_nav_area:hover {
  opacity: 1 !important;
}

.page_nav_area .page_num {
  display: inline-block;
  min-width:60px;
  padding:0 18px;
  font-style: normal;
  font-size: 13px;
  line-height: 32px;
  text-align:center;
  color: #999;
  vertical-align: top;
}
.page_nav_area .current_page{
  color:#2f9ccd;
}

.page_nav_area .btn_nav,
.page_nav_area .btn_exit_fullscreen{
  display:inline-block;
  position:relative;
  border:1px solid #9d9d9d;

  height:32px;
  line-height:32px;
}
.page_nav_area .btn_nav{
  @include border-radius(50%);

  width:32px;
  background:#fff;
}

.page_nav_area .btn_exit_fullscreen{
  @include border-radius(20px);

  margin-left:12px;
  padding:1px 9px 1px 33px;
  background:#fff;
  font-size:12px;
  color:#666;
  vertical-align:top;
  text-decoration:none;
}

.page_nav_area .btn_nav:hover,
.page_nav_area .btn_exit_fullscreen:hover{
  background-color:#64c1ea;
  border-color:#2f9ccd;
  color:#fff;
}

.page_nav_area .ico{
  display:inline-block;
  overflow:hidden;
  position:absolute;
  top:7px;
  left:10px;
  width:11px;
  height:18px;
  line-height:99px;
  vertical-align:top;
}
.page_nav_area .btn_prev_page .ico{
  @include use-sprite-image(-300px);
}
.page_nav_area .btn_next_page .ico{
  @include use-sprite-image(-270px);
}
.page_nav_area .btn_exit_fullscreen .ico{
  @include use-sprite-image(-210px);

  top:7px;
  width:20px;
  height:20px;
}

.page_nav_area .btn_prev_page:hover .ico{
  @include use-sprite-image(-720px);
}
.page_nav_area .btn_next_page:hover .ico{
  @include use-sprite-image(-750px);
}
.page_nav_area .btn_exit_fullscreen:hover .ico{
  @include use-sprite-image(-240px);
}

/* 사이트맵 컨트롤 버튼 */
.page_nav_area .btn_fd_sitemap{
  @include border-radius(50%);

  display:inline-block;
  position: relative;
  width:17px;
  height:17px;
  margin-left:5px;
  border:1px solid #9d9d9d;
  background-color:#c9c9c9;
  vertical-align: middle;

  cursor:pointer;
}
.page_nav_area .btn_fd_sitemap .ico{
  @include use-sprite-image(-780px);
  position: absolute;
  top:5px;
  left:2px;
  width:13px;
  height:9px;
}
.page_nav_area .btn_fd_sitemap.on{
  background-color:#64c1ea;
  border-color:#2f9ccd;

  .ico{
    @include use-sprite-image(-810px);
  }
}

/* 사이트맵 레이어 */
.ly_sitemap_area{
  position:absolute;
  top:34px;
  right:145px;
  width:162px;
  height:293px;
  margin-top:13px;
  padding:13px 0;
  background:#fff;
  border:1px solid #dedede;
}
.ly_sitemap_area:before{
  @include use-sprite-image(-690px);

  position:absolute;
  top:-11px;
  left:50%;
  width:24px;
  height:12px;
  margin-left:-12px;
  content:'';
}
.ly_sitemap_area ol{
  overflow-x:hidden;
  overflow-y: auto;
  width:100%;
  height:100%;
}
.ly_sitemap_area .sitemap_node{
  position: relative;
  padding:0 12px 0 19px;
}

.sitemap_node .sitemap_item{
  display:block;
  overflow:hidden;
  width:100%;
  font-size:11px;
  line-height:24px;
  color:#8f8f8f;
  white-space:nowrap;
  text-overflow:ellipsis;

  cursor:pointer;
}

.sitemap_node .sitemap_item:hover{
  color:#2f9ccd;
}
