@charset "UTF-8";

/* _frame_pc.scss */
body, input, textarea, select, button, table {
  font-family: '나눔고딕',nanumgothic,'돋움',Dotum,Helvetica,'Apple SD Gothic Neo',Sans-serif;
  font-size: 12px
}

body.pc{
  overflow:hidden;
  //min-width:980px;
  //min-height:600px;
}

body.pc button{
  cursor: pointer;
}
body.pc button:focus { outline:0;}

.content{
  min-width:320px;
  height: 100%;
}

/* 상단 메인 매뉴 */
.top_menu_bar{
  @include user-select(none);

  overflow: hidden !important;
  position: relative;
  height: 45px;
  background-color: #f3f3f3;
  border-bottom:1px solid #e4e4e4;
  text-align:center;
}
.top_control_menu_area{
  display:inline-block;
  position:relative;
}

.top_control_menu_area .btn_top_menu{
  display:inline-block;
  overflow:hidden;
  float:left;
  position: relative;
  width:52px;
  height:45px;
  line-height:45px;
  vertical-align:top;
}

.btn_top_menu{
  .ico{
    display:inline-block;
    position:absolute;
    height:22px;
    z-index:10;
    content:'';
  }
  .ico_interaction{
    @include use-sprite-image(-120px);

    top: 8px;
    left: 13px;
    width: 29px;
    height: 29px;
  }
  .ico.ico_qrcode{
    @include use-sprite-image(-150px);

    top:12px;
    left:13px;
    width:21px;
  }
  .ico_full_screen{
    @include use-sprite-image(-180px);

    top:12px;
    left:10px;
    width:21px;
  }
}

.btn_top_menu.on, .btn_top_menu:hover{
  .ico_interaction{
    @include use-sprite-image(-480px);
  }
  .ico.ico_qrcode{
    @include use-sprite-image(-510px);
  }
  .ico_full_screen{
    @include use-sprite-image(-540px);
  }
}

.btn_panel_ctrl{
  display:inline-block;
  position:absolute;
  top:0;
  width:27px;
  height:45px;
  padding:0;
  margin:0;
  border:0;
  background-color:#f3f3f3;
  -webkit-appearance:none;

  &:hover{
    background-color:#fff;
  }

  .ico{
    position:absolute;
    top:14px;
    left:4px;
    width:20px;
    height:20px;
  }

  &.btn_left{
    left:0;
    border-right:1px solid #e4e4e4;

    .ico{
      @include use-sprite-image(-600px);
    }
    &:hover .ico{
      @include use-sprite-image(-660px);
    }
    &.on .ico{
      @include use-sprite-image(-570px);
    }
    &.on:hover .ico{
      @include use-sprite-image(-630px);
    }
  }
  &.btn_right{
    right:0;
    border-left:1px solid #e4e4e4;

    .ico{
      @include use-sprite-image(-570px);
    }
    &:hover .ico{
      @include use-sprite-image(-630px);
    }
    &.on .ico{
      @include use-sprite-image(-600px);
    }
    &.on:hover .ico{
      @include use-sprite-image(-660px);
    }
  }
}

/* 패널별 공통 스타일  */
.panel_wrap{
  overflow:hidden;
  position:absolute;
  top: 10px;
  left: 10px;
  right: 8px;
  bottom: 0;
  background: #ccc;
}
.right_panel_container .panel_wrap{
  right:10px;
}

.panel_area{
  display:inline-block;
  overflow:hidden;
  position:relative;
  float:left;
  width:100%;
  height:50%;
}
.panel_area:after{
  display:block;
  overflow:hidden;
  content:"";
}

.panel_title{
  @include border-radius('3px 3px 0 0');

  overflow:hidden;
  position:relative;
  height:41px;
  padding-left:10px;
  background:#77caed;
  font-size:14px;
  line-height:42px;
  color:#0b5979;
  vertical-align:top;

  &:after{
    display: block;
    position: absolute;
    top: 18px;
    right: -67px;
    width: 300px;
    height: 200px;
    background: #64c1ea;
    content: '';
    transform: rotate(168deg);
    z-index:10;
  }

  > strong{
    display:inline-block;
    position:relative;
    font-weight:normal;
    z-index:20;
  }

  .btn_fold {
    @include border-radius(50%);
    @include use-sprite-image(-412px);

    display:inline-block;
    position: absolute;
    top: 7px;
    right: 7px;
    width:27px;
    height:27px;
    background-color: #2f9ccd;
    border: none;
    cursor:pointer;
    z-index:20;

    &.on{
      @include use-sprite-image(-441px);
    }
  }
}
.panel_content{
  @include border-radius('0 0 3px 3px');
  @include box-shadow(0, 2px, 0, rgb(176,176,176));

  overflow:auto;
  position:absolute;
  top:41px;
  left:0;
  right:0;
  bottom:10px;
  font-size:12px;
  background: #f6f6f6;
}

/* 왼쪽 패널 */
.left_panel_container{
  @include user-select(none);

  overflow:hidden !important;
  width:250px;
  background: #ccc;
}


/* 뷰어 콘텐츠 영역 */
.viewer_content_container{
  overflow:hidden !important;
  position:absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
}
.content_panel{
  position:absolute;
  top:46px;
  left:0;
  right:0;
  bottom:0;
}
.viewerPage_singleViews_area{
  position:absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
}

/* 우측 하단 다운로드 배너 영역 */
.btn_bnr_area{
  position: absolute;
  left: 0;
  right:0;
  bottom: 0;
  width:100%;
  height: 50px;
  background-color:#ccc;
  line-height:50px;
  vertical-align:top;
  white-space:nowrap;
  text-align:center;
  z-index:30;
}
.btn_bnr_area .bnr{
  display:inline-block;

  &:after{
    display:inline-block;
    width:72px;
    margin:14px 4px 0 3px;
    height:42px;
    background-image:url('../images/btm_bnr.png');
    background-repeat:no-repeat;
    vertical-align:top;
    content:'';
  }

  &.devcafe:after{
    background-position: -6px 0;
  }
  &.homepage:after{
    background-position: -88px 0;
  }
  &.facebook:after{
    background-position: -172px 0;
    margin-left:5px;
    width:55px;
  }
}

/* QR코드 레이어 */
.dimmd{
  position:fixed;
  top:0;
  bottom:0;
  left:0;
  right:0;
  background : rgba(0,0,0,.5);
  z-index:101;
}

.ly_QRcode{
  @include border-radius('3px');

  position: fixed;
  left: 50%;
  top: 50%;
  margin-top:-230px;
  margin-left:-208px;
  padding: 70px 30px 47px;
  background: #fff;
  font-size: 12px;
  line-height: 18px;
  color: #555;
  text-align: center;
  z-index: 200;
}
.ly_QRcode .input_copy_url{
  display:block;
  width:100%;
  padding:22px 0 10px;
  border:0;
  font-size: 18px;
  font-weight: bold;
  line-height:26px;
  text-align:center;
  text-decoration:underline;
  color:#497eec;
  -webkit-appearance: none;
}
.ly_QRcode .noti{
  font-size:13px;
  line-height:28px;
  color:#777;
  text-align: center;
  letter-spacing:-1px;
}
.ly_QRcode .btn_close{
  @include use-sprite-image(-360px);

  display:inline-block;
  position:absolute;
  top:18px;
  right:14px;
  width:25px;
  height:25px;
  border:0;
  background-color:transparent;
  line-height:999px;
  vertical-align:top;
}

/* 전체 화면 모드, 우/좌측 패널 감춤 모드 */
.full_screen .top_menu_bar,
.full_screen .left_panel_container,
.full_screen .aside_panel_container,
.full_screen .vsplitter,
.hidden_left_panel .left_panel_container,
.hidden_left_panel .vsplitter.left_split_bar,
.hidden_aside_panel .aside_panel_container,
.hidden_aside_panel .aside_panel,
.hidden_aside_panel .vsplitter.right_split_bar {
  display: none !important;
}
.full_screen .page_nav_area{
  display:block;
}
.full_screen .content_panel{
  top:0;
}
.full_screen .right_panel_container,
.hidden_left_panel .right_panel_container{
  left:0 !important;
}
.full_screen .right_panel_container,
.full_screen .viewer_content_container,
.hidden_aside_panel .viewer_content_container{
  width:100% !important;
}

/* 멀티뷰 기기 선택한 뷰어일 때  */
.multi_views .viewer_content_container{
  overflow-y:auto !important;
  background:#e3e5e8;
}
.multi_views .content{
  height:auto;
}
.multi_views .viewerPage_singleViews_area{
  display:none;
}

/* 패널별 접은 상태 */
.hidden_site_map_panel #sitemapPanel,
.hidden_multi_views_panel #multiViewsPanel,
.hidden_page_note_panel #pageNotePanel,
.hidden_widget_notes_panel #widgetNotesPanel{
  height:51px;

  .panel_content{
    display:none;
  }
  .panel_title{
    @include border-radius(3px);
    @include box-shadow(0, 2px, 0, rgb(176,176,176));
  }
}

.hidden_site_map_panel #multiViewList{
  bottom:8px;
}