@charset "UTF-8";

/** _frame_pc_nate.scss **/
.aside_panel_container{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  width:260px;
  background:#ccc;

}

.page_note_content{
  padding: 12px 20px;
  font-size: 11px;
  line-height: 22px;
  color: #555;
  word-break: break-all;
}

.list_widget_note .item_widget_note{
  padding:17px 13px;
  border-bottom:1px solid #d0e2e9;
  font-size:11px;
  line-height:22px;
  color:#666;
  //cursor:pointer;
}
.list_widget_note .item_widget_note.on{
  background:#e5f4f9;
}
.list_widget_note .widget_note_title{
  color:#666;
  font-size:13px;
  line-height:22px;
  font-weight:bold;
}

#widget_note_none{
  border:0;
  cursor:default;
}